$background_color: #124076;
$lighter_shade: #fcf4f7;
.profile {
  width: 100%;
  height: fit-content;
  padding-top: 70px;

  .inner_profile {
    width: 100%;
    height: fit-content;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;

    .profile_info {
      width: 100%;
      height: fit-content;
      display: flex;
      justify-content: center;
      align-items: flex-start;
      flex-direction: column;
      gap: 10px;
      font-size: 1vw;
      .name {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 5px;
        h4 {
          display: inline-block;
          font-weight: 500;
        }
        .value {
          font-weight: 500;
          color: rgb(36, 36, 36);
          border-bottom: 1px solid black;
        }
      }
      .role {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 5px;
        h4 {
          display: inline-block;
          font-weight: 500;
        }
        .value {
          font-weight: 500;
          color: rgb(36, 36, 36);
          border-bottom: 1px solid black;
        }
      }
      .job_profile {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 5px;
        h4 {
          display: inline-block;
          font-weight: 500;
        }
      }
      .role_management_button {
        padding: 8px 15px;
        border-radius: 5px;
        border: none;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
          rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
        cursor: pointer;
        background-color: $background_color;
        color: $lighter_shade;
      }
    }

    .user_heading {
      width: 100%;
      border-bottom: 1px solid black;
      font-size: 1vw;
    }

    .list_of_user_and_role {
      width: 100%;
      height: fit-content;

      table {
        border-collapse: collapse;
        font-size: 1vw;
        tbody {
          .column_name {
            th {
              padding: 8px 15px;
              .edit {
                cursor: pointer;
              }
            }
          }
          .column_value {
            th {
              padding: 8px 15px;
              font-weight: 400;
              .edit {
                cursor: pointer;
              }

              .dropdown {
                
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 5px;
                select {
                  height: 100%;
                  padding: 5px 15px;
                  font-size: 1vw;

                }
                button {
                  height: 100%;
                  padding: 6px 15px;
                  border-radius: 5px;
                  border: none;
                  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
                    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
                  cursor: pointer;
                  background-color: $background_color;
                  color: $lighter_shade;
                  font-size: 1vw;
                }
              }
            }
          }
        }
      }
    }
  }
}



@media screen and (max-width:2559px) and (min-width:1920px) {
  .profile{
      padding-top: 80px;
  }
}

@media screen and (min-width:2560px) {
  .profile{
      padding-top:80px ;
  }
}